.App {
  text-align: center;
}
@font-face {
  font-family: sahel;
  src: url(./fonts/Sahel-FD.ttf);
}
#root{
  direction: rtl;
  padding-right: 25px;
  padding-left: 25px;
  font-family: Sahel;
}
.bell{
    background-color: white !important;
    color: rgba(0,0,0,0.7) !important;
}
.bell:hover{
    background-color: rgba(0,0,0,0.1) !important;
}
.level{
    height: 40px;
    background-color: rgba(0,0,0,0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    width: 80%
}
.levelPort{
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    width:175px;
}
.levelPortCode{
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 30px;
    width:175px;
}
.levelPortCode:hover{
    background-color: rgba(0,0,0,0.1) !important;
}
.bg-gradient{
    background-image: linear-gradient(-135deg,#21254e,#232c58 19%,#232c58 0,#232c58 0,#232d5a 23%,#232d5a 0,#24305d 40%,#24305d 0,#24305d 0,#2b4a83 87%);
}
.avatar{
    border-radius: 0px 0px 30px 30px;
}
.t-white{
    color: white;
}

.c-pointer{
  cursor: pointer;
}
.bottom{
    border-left: 1px solid rgba(0,0,0,0.1);
    border-top: 1px solid rgba(0,0,0,0.1);
    border-right: 1px solid rgba(0,0,0,0.1);
}
.top-border{
    border-top: 1px solid rgba(0,0,0,0.1);
}
.w-35{
    width: 35%;
}
.w-15{
    width: 15%;
}
.input{
    height: 40px;
    width: 250px;
    border: none;
    border-radius: 30px;
    outline: none;
    padding-right:10px;
}
.doChange{
    width: 200px;
    height: 40px;
    border: none;
    outline: none;
    border-radius: 30px;
    background-color: rgb(46, 204, 113);
    color: white;
}
.address{
    min-height: 40px;
    width: 250px;
    border: none;
    border-radius: 30px;
    outline: none;
    padding-right:10px;
}
.h-10{
    height:10%;
}
.login{
    border: 1px solid rgba(0,0,0,0.1);
    height: 35px;
    padding-right: 5px;
    border-radius: 8px;
    outline: none;
}